@mixin default-2 {
  position: $position;
  background-color: $base-color;
}

@mixin default-size {
  width: $base-size;
  height: $base-size;
  margin: auto;
  position: $position;
  background-color: $base-color;
}

@mixin minsec-position {
  height: 100%;
  position: $position;
  left: 0;
  right: 0;
  margin: auto;
}
